import React, { useState } from "react";
import styled, { keyframes } from 'styled-components';
import { slideInLeft } from 'react-animations';
import StaffCard from "../../components/staff_card/staff_card";

const Staff = () => {
  const [timer, setTimer] = useState(1);
  // Animationed Components
  const fadeInAnimation = keyframes`${slideInLeft}`;
  let FadeDiv = styled.div`
    animation: ${timer}s ${fadeInAnimation};
  `;
  const maint = {
    title: "Event Worker",
    img: "data:image/jpeg;base64,",
    name: "Vacant",
    description:
      "The person in this position will perform skilled and un-skilled related work in County Park facilities. Operates light to medium size construction equipment in the maintenance of Park grounds and facilities.This person will also assist with park events. ",
  };

  let workers = [];
  for (let i = 0; i < 5; i++) {
    workers.push(maint);
  }
  
  return (
    <div className="container-fluid mt-3">
      <div className="row justify-content-center">
        <StaffCard
          title="Facility Manager"
          name="Khip Miller"
          img="data:image/jpeg;base64,"
          description="Khip has been employed with Hall County for 20 plus years. He served the county as a Marshal for several years and now presides over the Chicopee Woods Agricultual Center. "
        />
      </div>

      <div className="row justify-content-center">
        <StaffCard
          className="col-6"
          title="Event Coordinator"
          name="Dwight Mobley"
          img="data:image/jpeg;base64,"
          description="Dwight was previously employeed with the Marshals' office. He now acts as the event coordinator for the Chicopee Woods Agricultural Center. He enjoys training horses and trail rides. "
        />
      </div>

      <div className="row justify-content-center">
        <StaffCard
          className="col-lg-4"
          title="Foreman"
          name="Vacant"
          description="The person in this position will develop and implement the maintenance of the parks and leisure facilities on a daily, weekly, and seasonal basis. Performs tasks in grounds, vehicle, building, and equipment maintenance and supervises inmates in the performance of maintenance tasks. "
          img="data:image/jpeg;base64,
        "
          vacant={true}
        />
        {workers.map((worker, key) => {
         let count = workers.indexOf(worker);
         console.log(count)
         return(
           <FadeDiv key={key} className="col-lg-4">
            <StaffCard              
              title={worker.title}
              name={worker.name}
              img={worker.img}
              description={worker.description}
              vacant={true}
            />
            </FadeDiv>
         )
          
        })}
      </div>
    </div>
  );
};

export default Staff;
